import React from 'react';
import styled from 'styled-components';

// TODO extract to a better place
interface FocusableProps {
  focused: boolean;
  hasFocusedChild?: boolean;
}

export const ShowDescription = styled.div`
  color: white;
  opacity: 0.8;
  font-size: 1.25rem;
  transition: all 0.5s ease-in-out;
  height: 3rem;
`;

export const SpotlightContainer = styled.div<FocusableProps>`
  padding: 16rem 5rem 1.5rem;
  transition: all 0.5s ease-in-out;
  ${({ hasFocusedChild }) =>
    hasFocusedChild &&
    `
    margin-bottom: 3rem;
    padding: 24rem 5rem 3rem;
    ${ShowDescription} {
      margin-bottom: 2rem;
    }
  `};
`;

export const ShowTitle = styled.h1`
  color: white;
  opacity: 0.9;
  font-size: 3rem;
  margin-top: 0;
`;

export const PlayButton = styled.span<FocusableProps>`
  display: inline-block;
  padding: 0.75rem 1rem;
  border-radius: 2.5rem;
  color: black;
  background-color: white;
  opacity: 0;
  font-size: 1.5rem;
  ${({ focused }) =>
    focused &&
    `
    opacity: 1;
    box-shadow: 0 0 0 2pt white;
  `}
  transition: all .35s ease-in-out;
`;

const SvgLogo = styled.svg`
  margin-bottom: 0.5rem;
  margin-left: 0.25rem;
  fill: #fff;
  opacity: 0.8;
`;

